<template>
  <div id="about" class="section welcome-container rounded mt-24 mb-8 mx-8 p-8 bg-primary-200 flex flex-col sm:grid sm:grid-cols-3 justify-center">

    <div class="welcome-text sm:m-8 flex flex-col col-span-1 sm:text-center justify-center items-center">
      <h2 class="text-2xl text-poppy-500">Welcome to <span class="text-5xl text-secondary-400 uppercase font-semibold">NBC Vungtau</span></h2>
      <p class="text-lg my-8 text-justify sm:text-left">Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quasi mollitia vel quod provident dicta corporis, praesentium delectus. At, adipisci exercitationem?</p>
    </div>

    <!-- slideshow -->
    <div class="slide-show sm:m-8 col-start-2 col-span-2">

      <div class="welcome-img fade bg-elock1"></div>
      <div class="welcome-img fade bg-elock2"></div>
      <div class="welcome-img fade bg-elock3"></div>

      <!-- next & previous button -->
      <a class="prev" @click="plusSlide(-1)">&#10094;</a>
      <a class="next" @click="plusSlide(1)">&#10095;</a>

      <!-- slideshow circles -->
      <div class="dots text-center">
        <span class="dot" @click="currentSlide(1)"></span>
        <span class="dot" @click="currentSlide(2)"></span>
        <span class="dot" @click="currentSlide(3)"></span>
      </div>

    </div>

  </div>
</template>

<script>
export default {
  name: 'Welcome',
  data() {
    return {
      slideIndex: 1
    }
  },
  mounted() {
    this.carousel()
  },
  methods: {

    plusSlide(n) {
      this.showSlide(this.slideIndex += n)
    },

    currentSlide(n) {
      this.showSlide(this.slideIndex = n)
    },

    showSlide(n) {
      let slides = document.querySelectorAll('.welcome-img')
      let dots = document.querySelectorAll('.dot')

      if (n > slides.length) {this.slideIndex = 1}
      if (n < 1) {this.slideIndex = slides.length}

      slides.forEach(slide => {
        slide.style.display = 'none'
      })

      dots.forEach(dot => {
        dot.classList.remove('active')
      })

      slides[this.slideIndex - 1].style.display = 'block'
      dots[this.slideIndex - 1].classList.add('active')
    },

    carousel() {
      let slides = document.querySelectorAll('.welcome-img')
      let dots = document.querySelectorAll('.dot')

      slides.forEach(slide => {
        slide.style.display = 'none'
      })

      dots.forEach(dot => {
        dot.classList.remove('active')
      })

      this.slideIndex++
      if (this.slideIndex > slides.length) {this.slideIndex = 1}
      slides[this.slideIndex - 1].style.display = 'block'
      dots[this.slideIndex - 1].classList.add('active')

      setTimeout(this.carousel, 3000)
    }
  }
}
</script>

