      <div id="nav-mobile" class="nav-bar hidden md:col-start-2 md:col-span-5">
        <ul class="justify-center md:justify-start py-4 uppercase font-medium">
          <li class="nav-link px-4 hover:text-primary-500 cursor-pointer" @click="onClickMenu('about')"><a href="#about">Giới thiệu</a></li>
          <li class="nav-link px-4 hover:text-primary-500 cursor-pointer" @click="onClickMenu('services')"><a href="#services">Dịch vụ</a></li>
          <li class="nav-link px-4 hover:text-primary-500 cursor-pointer" @click="onClickMenu('products')"><a href="#products">Sản phẩm</a></li>
          <li class="nav-link px-4 hover:text-primary-500 cursor-pointer" @click="onClickMenu('showcase')"><a href="#showcase">Dự án</a></li>
        </ul>
      </div>

    </div>

