<script>

export default {
  name: 'Navbar',
  data() {
    return {
      isSticky: false
    }
  },
  mounted() {
    // get position to make sticky navbar
    let position = document.getElementById('nav-container')

    //
    let sections = document.querySelectorAll('.section')
    let navLinks = document.querySelectorAll('.nav-bar ul li a')

    // making navbar sticky
    window.document.onscroll = () => {
      let navBar = document.getElementById('menu-bar')
      
      if (window.scrollY > position.offsetHeight) {
        navBar.classList.remove('menu-bar')
        navBar.classList.add('menu-bar-sticky')
      } else {
        navBar.classList.remove('menu-bar-sticky')
        navBar.classList.add('menu-bar')
      }
    }

    // navbar current section
    window.addEventListener('scroll', e => {
      let fromTop = window.scrollY

      navLinks.forEach(link => {
        let section = document.querySelector(link.hash) //return div based on anchor's href
        
        if (
          section.offsetTop - 150 <= fromTop && // offset including navbar
          section.offsetTop + section.offsetHeight - 150 > fromTop 
        ) {
          link.classList.add('li-current')
        } else {
          link.classList.remove('li-current')
        }
      })
    })

    // remove menu by clicking outside nav
    // window.addEventListener('click', e => {
    //   let nav = document.getElementById('nav-sticky')
    //   nav.classList.toggle('nav-mobile')
    // })

  },
  methods: {
    //
    onClickMenu (e) {
      let position = document.getElementById(e)
      window.scrollTo({
        top: position.offsetTop - 60,
        left: 0,
        behavior: 'smooth'
      })
    },
    //
    onClickHamburger () {
      let nav = document.getElementById('nav-mobile')
      let hamburgers = document.querySelectorAll('.bar-line')

      nav.classList.toggle('hidden')
      nav.classList.toggle('nav-mobile')

      hamburgers.forEach(bar => {
        bar.classList.toggle('change')
      })

    }
  }
}
</script>

